import React from 'react';
import C from './Calc.css';

class Calc extends React.Component{
	constructor(props){
		super(props);
		this.state={
			result: 0
	}
	}
	static getDerivedStateFromProps(props,state){
		return {rate: props.rate}
	}
		submitValue = (e) => {
		e.preventDefault();
		console.log('work');
		let elements = e.target.elements;
		console.log(elements);
		console.log(elements['count-currency'].value);
		console.log(elements['type-currency'].value);
		let countCurrency = elements['count-currency'].value;
		let typeCurrency = elements['type-currency'].value;
		this.setState({result: (countCurrency/this.state.rate[typeCurrency]).toFixed(2)})
	}
	render(){
		return (
				<div className='calc'>
					<h3 className='calResult'>Exchange calculator</h3>
					<div>Ammount to convert to dollars</div>
					<div>
					<form  onSubmit={this.submitValue}>
					<input className="calcInput" type="number" min='0' placeholder="enter ammount" name='count-currency'/>
					<select className="calcSelect" name='type-currency' id=''>
					{
						Object.keys(this.props.rate).map((keyName,i) =>
							(
								<option className="calcInput" key={keyName} value={keyName}>{keyName}</option>
							)
						)
					}
					</select>
					<input className="calcInputResult"  type="submit"  value='result'/>
					</form>
					</div>
					<div>
						<h4 className='calResult'>Your result</h4>
						<ul>
							 <h3 className='mainResult'><li>
								 {this.state.result===0 ? 'result here' : this.state.result}</li></h3>
						</ul>
						
					</div>
					
				</div>
			);
	}
}

export default Calc;