 
import React from 'react';
import H from './Header.css';
import {
  Link,
  NavLink
} from "react-router-dom";
 class Header extends React.Component{
    constructor(){
        super();
        this.state={button: true}
    }
     funcChange(){
        this.setState({button:!this.state.button})
    }
    render(){
        return (
              <header>
                <span className="span">Currency Converter</span>
                <nav>
                <input id="menu_check" type="checkbox"/>
                                <label htmlFor="menu_check" className="menu_btn" checked onClick={this.funcChange.bind(this)}>
                                {this.state.button ?<i className="fas fa-bars" /> : <i class="far fa-times-circle" />}
                                </label>
                    <ul className="menu_box">
                        <NavLink className="liFirst " exact activeStyle={{ color:'red'}} to="/" >Home</NavLink>
                        <NavLink className="liAnother" activeClassName="active" to="/about" >About Us</NavLink>
                        <NavLink className="liAnother" activeClassName="active" to="/contact" >Contacts</NavLink>
                      
                    </ul>
                </nav>
            </header>
           
            )
    }
 
        }
        export default Header;