import React, {Fragment,Component} from 'react';
import A from './About.css';
import Adres from  '../Adres/Adres.js';

import png from './IMG_20170315_132748.jpg'

class Tab1 extends React.Component{
  constructor(props){
    super()
    this.state= {
      name:[
      {obmen:'Обменный пункт №107:', plase: 'г. Гродно, пр. Я. Купалы, 16а, ТЦ "Корона'},
      {obmen:'Обменный пункт №108:', plase: 'г. Гродно, пр. Я. Купалы, 16а, ТЦ "Корона'},
      {obmen:'Обменный пункт №126:', plase: 'г. Гродно, ул. Советская, 18 '},
      {obmen:'Обменный пункт №116:', plase:  'г. Гродно, ул. Лиможа, 30'},
      ]
    };
  }
  render(){
    let result = this.state.name.map((elem,index)=>{
          return <Adres 
            key = {index}
            obmen={elem.obmen}
            plase={elem.plase}
        />
        })
      return(
          <>
          <h1 className='nameAbout'>exchange offices in grodno</h1>
          <ul>{result}</ul>
        </>
     )
  }
}

const Tab2 = () =>(
  <div className='tabStyle1'>
 <img className="styleTab1" src={png}/>
 <h1 className='tab1'>Developer is Furs Pavel.<br/>Experience in web development one years.</h1>
 
 </div>
)

class About extends Component{
  state={
      activeValue:1
  }
      handlerTab =(e)=>{
        this.setState({activeValue:+e.target.getAttribute('data-name')})
      }
      render(){
        const {activeValue} = this.state;
        return(
            <Fragment>
            <div className="about">
              <button data-name={1} onClick={this.handlerTab}>information</button>
              <button data-name={2} onClick={this.handlerTab}>developer</button>
              {activeValue===1 && <Tab1 />}
              {activeValue===2 && <Tab2 />}
              <div className='activTab'>{`active: ${activeValue===1  ? "INFORMATION" : "DEVELOPER"}`}</div>
            </div>
            </Fragment>
          )
      }
}

export default About;




