import React from 'react';
import ReactDOM from 'react-dom';
import Calc from '../Calc/Calc.js';
import I from './Info.css';
function addZerro(x){
 	   	if(x < 10){
 	   		console.log('0'+x)
 	   		return '0'+x;
 	   	} else {
 	   		return x;
 	   	}
 	   }
class Info extends React.Component{
 constructor(){
 	super();
 	this.state={
 		'date':'',
 		'currancyRate':{},
 		'timestamp':'',
 		value1: new Date().getFullYear(),
 		value2: addZerro(new Date().getMonth()+1),
        value3:new Date().getDate(),
 		 }
 	this.currancy = ['BYN','RUB','EUR','PLN'];
 	this.getRate();
 	
 }
 		
 getRate = () => {
 	
 	/*fetch(`https://openexchangerates.org/api/historical/${this.state.value1}-${this.state.value2}-${this.state.value3}.json?app_id=62d0a84b96ce425cb8c0d318079a7219`)*/
 	 
 	fetch('https://openexchangerates.org/api/latest.json?app_id=62d0a84b96ce425cb8c0d318079a7219')
 	  .then(data => {
 	  	return data.json();
 	  	console.log(data);
 	  })
 	  .then(data => {
 	  	console.log(data);
 	  	this.setState({date:data.date});
 	  	let result = {};
 	  	for(let i =0; i<this.currancy.length; i++){
 	  		result[this.currancy[i]] = data.rates[this.currancy[i]]
 	  	}
 	  	/*console.log(result);*/
 	  	this.setState({currancyRate:result});
 	  	})
 	  this.functionChangeDate =()=>{
 	  	this.getRate();
 	   }
 

 	}
 	
 	  	//<div> {this.state.currancyRate[keyName].toFixed(2)}*</div>
	render(){

		return (
				<main className='main'>
					 <h3 className='kursValut'>Currency Converter {this.state.value1}-{this.state.value2}-{this.state.value3}</h3>
					
						<div className="section">
						        {Object.keys(this.state.currancyRate).map((keyName,i)=>
							(<div key={keyName} className='info a'>
								<div>{keyName}</div>
								<div> {this.state.currancyRate[keyName].toFixed(2)}*</div>
								<p>* you can buy for one dollar </p>
						    </div>
							))}
						</div>
						<Calc rate = {this.state.currancyRate}/>
						
				</main>
			)
	}

}
export default Info;
