					/*<input className='changeValue firstInput' type='text'  value={this.state.value1} onChange={(event)=>this.setState({value1:event.target.value})} />
					<input className='changeValue secondtInput' type='text' value={this.state.value2} onChange={(event)=>this.setState({value2:event.target.value})} />
					<input className='changeValue secondtInput' type='text' value={this.state.value3} onChange={(event)=>this.setState({value3:event.target.value})} />
					<button className='changeValue secondtInput' onClick={this.functionChangeDate.bind(this)}>change data</button>*/